import {BaseApiUrl, basicDataBaseApiUrl} from '../constants/baseConfig';


const WebApiConfig = {
    application: {
        Base: BaseApiUrl + 'Application',//
        List: BaseApiUrl + 'Application/list',//
    },
    user: {
        Base: BaseApiUrl + 'User',//
        RestPwd: BaseApiUrl + 'user/resetpassword' //
    },
    message: {
        unReadCount: basicDataBaseApiUrl + 'messages/unreadcount',//
        receiveList: basicDataBaseApiUrl + 'messages/readlist',//
        getMsgDetail: basicDataBaseApiUrl + 'messages/',//
    },
    permission: {
        judgePermssion: BaseApiUrl + 'Permission/each',//
    }, dic: {
        ParList: basicDataBaseApiUrl + 'dictionarydefines/list',//  get
        AreaList: basicDataBaseApiUrl + "areadefines/list"//
    }
}

export default WebApiConfig;


